
































































































































































































































































































.screen {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background: url('../assets/wallpaper.jpg') no-repeat center;
  background-size: 100% auto;
  display: flex;
  flex-direction: column;
  .desktop-content {
    flex: 1;
    box-sizing: border-box;
  }
  .desktop-taskbar {
    width: inherit;
    background: rgba($color: #fff, $alpha: 0.3);
    cursor: default;
    position: relative;
    z-index: 2;
    .taskbar-left {
      float: left;
      position: relative;
      .taskbar-startmenu {
        width: 500px;
        height: 500px;
        border: 1px solid #fff;
        background: rgba($color: #fff, $alpha: 0.2);
        position: absolute;
        top: -500px;
        z-index: 1;
      }
      .taskbar-left-ul {
        li {
          display: inline-block;
          padding: 10px;
          transition: all 0.15s;
          i {
            font-size: 20px;
          }
          &:hover {
            color: #fff;
            background-color: rgba($color: #fff, $alpha: 0.2);
            opacity: 0.7;
          }
        }
      }
    }
    .taskbar-right {
      float: right;
      position: relative;
      .taskbar-right-ul {
        display: flex;
        justify-content: center;
        li {
          display: inline-block;
          padding: 10px;
          transition: all 0.15s;
          &:last-child {
            margin-left: 5px;
            padding: 10px 4px;
            width: 0;
            background: rgba($color: #fff, $alpha: 0.08);
            box-shadow: 7px 3px 15px rgba(0, 0, 0, 0.07);
          }
          &.sysTime {
            padding: 0;
          }
          .sysTime-div {
            display: flex;
            flex-direction: column;
            align-items: center;
            transform: scale(0.8, 0.8) translateZ(0);
          }
          i {
            font-size: 14px;
          }
          &:hover {
            color: #fff;
            background: rgba($color: #fff, $alpha: 0.2);
            opacity: 0.7;
          }
        }
      }
    }
  }
}
